import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IS4AccountMapGetAllModel,
  IS4AccountMapModel,
  IS4AccountMapRequest,
} from "../models/s4-account-map";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { IPageParams } from "app/shared/interfaces/params";
import { map } from "rxjs/operators";
import { s4AccountGetAllToMap } from "../mapper/s4-account-map";

@Injectable({
  providedIn: "root",
})
export class S4AccountMapService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IS4AccountMapRequest) {
    return this.post<IResponse<IS4AccountMapModel>>(
      "S4AccountMapSetup/Create",
      data
    );
  }

  update(data: IS4AccountMapRequest) {
    return this.post<IResponse<IS4AccountMapModel>>(
      "S4AccountMapSetup/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IS4AccountMapModel[]>>(
      "S4AccountMapSetup/GetList",
      {
        params: {
          ...params,
          $expand: db.PAY_Variable,
        },
      }
    );
  }

  getAll() {
    return this.get<IResponse<IS4AccountMapGetAllModel[]>>(
      "S4AccountMapSetup/GetAll"
    ).pipe(
      map((res: IResponse<IS4AccountMapGetAllModel[]>) => {
        return s4AccountGetAllToMap(res.data);
      })
    );
  }

  getById(id: number) {
    return this.get<IResponse<IS4AccountMapGetAllModel[]>>(
      "S4AccountMapSetup/GetAll",
      {
        params: {
          $filter: `id eq ${id}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("S4AccountMapSetup/Delete", {
      params: { Id: Id },
    });
  }

  exportExcel() {
    return this.getAll();
  }
}
