import { msgTranslate } from "app/shared/resources/helpers-functions";
import {
  IS4AccountMapGetAllModel,
  IS4AccountMapModel,
} from "../models/s4-account-map";

export function s4AccountGetAllToMap(
  violations: IS4AccountMapGetAllModel[]
): IS4AccountMapModel[] {
  return violations.map((s4Account) => {
    return <IS4AccountMapModel>{
      ...s4Account._I_PAY_Variable_SAP_SystemInstance_AccountNumber_COMM_Bank,
      organizationTypeId: s4Account.orgTypeId,
    };
  });
}


export function s4AccountToMap(
  violations: IS4AccountMapModel[]
): IS4AccountMapModel[] {
  return violations.map((s4Account) => {
    return <IS4AccountMapModel>{
      ...s4Account,
      integrationPurpose: s4Account.s4_IntegrationPurpose.name,
      accountType: s4Account.is_Debit ? "Debit" : "Credit",
      variables: msgTranslate(
        s4Account.pay_Variable?.arName,
        s4Account.pay_Variable?.name
      ),
      bothBankCash: s4Account.bothBankCash && !s4Account.bank_ID ? true : false,
    };
  });
}
