import { S4SystemInstanceIndexComponent } from "./components/s4-system-instance/s4-system-instance-index/s4-system-instance-index.component";
import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { S4AccountMapIndexComponent } from "./components/s4-account-map/s4-account-map-index/s4-account-map-index.component";
import { AddS4AccountMapComponent } from "./components/s4-account-map/add-s4-account-map/add-s4-account-map.component";
import { AddS4SystemInstanceComponent } from "./components/s4-system-instance/add-s4-system-instance/add-s4-system-instance.component";
import { pathMatch } from "app/shared/enums/route-path-match.enum";
import { ViewsManager } from "app/shared/resources/views-manager";

const routes: Routes = [
	{
		path: "s4-system-instance",
		data: { view:  ViewsManager.add_s4_system_instance },
		children: [
			{
				path: "",
				redirectTo: "index",
				pathMatch: pathMatch.full,
			},
			{
				path: "index",
				component: S4SystemInstanceIndexComponent,
				data: { view:ViewsManager.s4_system_instance_index},
			},
			{
				path: "add",
				component: AddS4SystemInstanceComponent,
				data: { view: ViewsManager.add_s4_system_instance},
			},
			{
				path: "edit/:s4SystemInstanceId",
				component: AddS4SystemInstanceComponent,
				data: { view:ViewsManager.add_s4_system_instance },
			},
		],
	},
	{
		path: "s4-account-map",
		data: { view: ViewsManager.s4_account_map },
		children: [
			{
				path: "",
				redirectTo: "index",
				pathMatch: pathMatch.full,
			},
			{
				path: "index",
				component: S4AccountMapIndexComponent,
				data: { view: ViewsManager.s4_account_map_index},
			},
			{
				path: "add",
				component: AddS4AccountMapComponent,
				data: { view: ViewsManager.add_s4_account_map},
			},
			{
				path: "edit/:s4AccountMapId",
				component: AddS4AccountMapComponent,
				data: { view: ViewsManager.add_s4_account_map },
			},
		],
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class S4SetupRoutingModule {}
