import { Component, OnInit } from "@angular/core";
import { FormGroup } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { IS4SystemInstanceModel } from "app/s4-setup/models/s4-system-instance";
import { S4SytemInstanceService } from "app/s4-setup/services/s4-sytem-instance.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-s4-system-instance-index",
	templateUrl: "./s4-system-instance-index.component.html",
	styleUrls: ["./s4-system-instance-index.component.scss"],
})
export class S4SystemInstanceIndexComponent extends AppHelpers implements OnInit {
	form: FormGroup;
	mandatoryColomns: string[] = ["Ser", "id", "ar_Name", "en_Name", "api_Url_Prefix", "actions"];
	optionalColomns: string[] = ["userName", "password", "s4_Client_Code", "s4_Company_Code"];

	dataSource: MatTableDataSource<IS4SystemInstanceModel>;
	isLoaded: boolean = false;

	constructor(public s4SytemInstanceService: S4SytemInstanceService) {
		super();
	}

	ngOnInit(): void {
		// if (this.CheckNoBusinessOrCompany()) this.getList(this.currentPage);

		this.form = this.fb.group({
			systemInstance_ID: [null],
			integrationPurpose_ID: [null],
			LG_Account: [null],
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.checkNoBusinessOrCompany())
			this.getList(this.pageFromUrl > 0 ? this.pageFromUrl : this.firstPage);
	}

	searchList() {
		if (
			!this.form?.value.systemInstance_ID &&
			!this.form?.value.integrationPurpose_ID &&
			!this.form?.value.LG_Account
		) {
			this.getList(this.firstPage);
			return;
		} else {
			let filter = {
				$filter: "",
			};
			if (this.form?.value.LG_Account)
				filter.$filter = `LG_Account eq ${this.form.value.LG_Account}`;
			else if (this.form?.value.integrationPurpose_ID)
				filter.$filter = `IntegrationPurpose_ID eq ${this.form.value.integrationPurpose_ID}`;
			else if (this.form?.value.systemInstance_ID)
				filter.$filter = `systemInstance_ID eq ${this.form.value.systemInstance_ID}`;
			this.getList(this.firstPage, filter);
		}
	}

	getList(page: number, filter = {}, pageSize?: number) {
		this.removePageParams();
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		let params: IPageParams = {
			PageNumber: page,
			pageSize: this.page.pageSize,
			...filter,
		};
		this.s4SytemInstanceService
			.getList(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IS4SystemInstanceModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					if (res.data.length == 0 && page > 1) {
						this.getList(this.prevPage);
						return;
					}
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
					this.page.length = res.meta.totalItemCount;
				} else this.errorList(res.message.messages);
			});
	}

	onDelete(s4SystemInstance: IS4SystemInstanceModel): void {
		let msg = this.msgTranslate(s4SystemInstance.ar_Name, s4SystemInstance.en_Name);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(s4SystemInstance);
		});
	}

	delete(s4SystemInstance: IS4SystemInstanceModel) {
		this.showLoading();
		this.s4SytemInstanceService.Delete(s4SystemInstance.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				if (this.dataSource.data.length == 1 && this.currentPage > 1) {
					// if last item in list: get previous page
					this.getList(this.prevPage, this.page.pageSize);
				} else {
					// if not last item in list: get current page
					this.getList(this.currentPage, this.page.pageSize);
				}
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	resetSearch() {
		this.form.reset();
		this.getList(this.firstPage);
	}
}
